.container {
  text-align: center;
  padding: 20px;
}

.title {
  font-size: 2em;
  color: #333;
}

.description {
  font-size: 1.2em;
  color: #666;
}

.button {
  padding: 10px 20px;
  font-size: 1em;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.button:hover {
  background-color: #0056b3;
}